
import { defineComponent } from 'vue';
import { FeeByCustomerList, PFM20400111_REQ_BODY, PFM20400111_RES } from '@/shared/type/PFM20400111.type';
import Pagination from '@/shared/components/Pagination/Pagination.vue';
import { PAGE_SIZE } from '@/shared/constants/common.const';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import '@vuepic/vue-datepicker/dist/main.css'
import VueDatePicker from '@vuepic/vue-datepicker';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";


export default defineComponent({
	name: 'pfm204001sc00',
	components: {
		BreadCrumb,
		Pagination,
		VueDatePicker,
		SelectOption
	},
	data() {
		const headers = [
			{ columnName: "Fee Code" },
			{ columnName: "Customer No" },
			{ columnName: "Customer Name" },
			{ columnName: "Fee Description" },
			{ columnName: "Apply Seq No" },
			{ columnName: "Fee CurrencyCode" },
			{ columnName: "Apply Start Date" },
			{ columnName: "Apply End Date" },
			{ columnName: "Base Fee Type" },
			{ columnName: "Base Fee Rate %" },
			{ columnName: "Base Fee Amount" },
			{ columnName: "Fee Min Amount" },
			{ columnName: "Fee Max Amount" },
			{ columnName: "Base Fee Apply Start Amount" },
			{ columnName: "Fee Reason Code" },
			{ columnName: "Fee Apply Status Code" },
		];
		return {
			dataTable: [] as FeeByCustomerList[],
			headers,
			searchField: {
				customerNo: "",
				customerName: "",
				applyStartDate: '' as unknown,
				applyEndDate: '' as unknown,
				feeApplyStatusCode: '',
			},
			totalItems: 0,
			currentPage: 1,
			pageSize: PAGE_SIZE,
			startingIndex: 1,
			isResetPage: true
		}
	},

	computed: {
		formattedStartDate(): string {
			return this.searchField.applyStartDate ? this.$datetime.formatVueDatePicker(this.searchField.applyStartDate as Date) : ''
		},

		formattedEndDate(): string {
			return this.searchField.applyEndDate ? this.$datetime.formatVueDatePicker(this.searchField.applyEndDate as Date) : ''
		},

		covertCodeValue() { 
            return (covertCodeType: string, baseCode: string) => `${baseCode}` +' : '+ this.$codeConverter.convertValue(`${covertCodeType}`, `${baseCode}`); 
        }
	},

	created() {
		this.getFeeByCustomerList(false)
	},

	methods: {
		async getFeeByCustomerList(isSearch: boolean) {
			const reqbody: PFM20400111_REQ_BODY = {
				customerNo: this.searchField.customerNo.trim(),
				customerName: this.searchField.customerName.trim(),
				feeApplyStatusCode: this.searchField.feeApplyStatusCode,
				applyStartDate: this.searchField.applyStartDate ? this.searchField.applyStartDate ? this.$datetime.convertDateToString(this.searchField.applyStartDate as Date) : '' : '',
				applyEndDate: this.searchField.applyEndDate ? this.searchField.applyEndDate ? this.$datetime.convertDateToString(this.searchField.applyEndDate as Date) : '' : '',
				pageSize: this.pageSize,
				pageNumber: isSearch ? 0 : this.currentPage,
			}

			if(isSearch){
				this.currentPage = 1
				this.isResetPage = true
			}

			// ---- Adapter: PFM20400111_Adapter_FeeByCustomer_InquiryList
			const resData = await this.$request.getRequest('PFM20400111', reqbody, false) as PFM20400111_RES
			
			this.dataTable = JSON.parse(JSON.stringify(this.convertCodeToValue(resData.body.list)))
			this.totalItems = resData.body.totalCount
		},

		onClickRegister() {
			this.$router.push({ 
				path: '/PFM204001SC01', 
				query: {
					type: 'register'
				}})
		},

		handleRowClick(index, rowData: FeeByCustomerList) {
			this.$router.push({
				path: '/PFM204001SC01',
				query: {
					type: 'edit',
					productFeeCode: rowData.productFeeCode,
					feeCurrencyCode: rowData.feeCurrencyCode,
					applyStartDate: this.$datetime.unFormatStringToStringDate(rowData.applyStartDate),
					customerNo: rowData.customerNo,
					applySeqNo: rowData.applySeqNo
				}
			})
		},

		async onClickReset() {
			this.searchField.customerNo = "";
			this.searchField.customerName = "";
			this.searchField.applyStartDate = ''
			this.searchField.applyEndDate = ''
			this.searchField.feeApplyStatusCode = ''
			this.isResetPage = true
			this.currentPage = 1

			await this.getFeeByCustomerList(false)
		},

		convertCodeToValue(data: FeeByCustomerList[]): FeeByCustomerList[] {
			return data.map((record: FeeByCustomerList) => {
				return {
					customerNo: record.customerNo,
					customerName: record.customerName,
					productFeeCode: record.productFeeCode,
					feeDescription: record.feeDescription,
					feeCurrencyCode: record.feeCurrencyCode,
					applySeqNo: record.applySeqNo,
					baseFeeApplyStartAmount: this.$format.formatCurrency(record.baseFeeApplyStartAmount, record.feeCurrencyCode),
					feeMinAmount: this.$format.formatCurrency(record.feeMinAmount, record.feeCurrencyCode),
					applyStartDate: this.$datetime.formatStringToStringDate(record.applyStartDate),
					applyEndDate: this.$datetime.formatStringToStringDate(record.applyEndDate),
					applyTransactionFeeAmount: this.$format.formatCurrency(record.applyTransactionFeeAmount, record.feeCurrencyCode),
					feeApplyStatusCode: this.covertCodeValue('FeeApplyStatusCode', record.feeApplyStatusCode),
					transactionFeeTypeCode: this.covertCodeValue('TransactionFeeTypeCode', record.transactionFeeTypeCode),
					feeFavorReasonCode: this.covertCodeValue('specialFeeReasonCode', record.feeFavorReasonCode),
					applyFeeRate: record.applyFeeRate,
					feeMaxAmount: this.$format.formatCurrency(record.feeMaxAmount, record.feeCurrencyCode),
					minFeeRate: record.minFeeRate,
					maxFeeRate: record.maxFeeRate
				}
			})
		},

		handlePagination(pageNumber: number) {
			this.isResetPage = false;
			this.currentPage = pageNumber
			this.getFeeByCustomerList(false)
		},

		handleStartingIndex(startingIndex: number){
			this.startingIndex = startingIndex
		},

		indexMethod(index: number) : number {
			return this.startingIndex + index
		}
	}
})
